<template>
    <td class="text-center mobile-action">
        <span class="actions mobileaction"> 
            <em class="fa fa-ellipsis-h action_icon" aria-hidden="true"></em>
            <span class="action-list-box">
                <span class="list-actions" id="action-list">
                    <template v-for="(action,actionIndex) in listActions">
                        <a v-if="action.isVisible &&  typeof action.callbackfunction != 'undefined'" :key="actionIndex"
                            class="actions-onclick" v-on:click="ClickEvent(action.callbackfunction,item,index,$event)"
                            :typetitle="action.title" :typefor="action.title" v-bind:value="item[identityColumn]"
                            :title="action.title" >
                            <em :class="action.icon"></em>
                            <span class="text"> {{action.title}} <span
                                    v-html="replaceText(action.additionalSpan)"></span></span>
                        </a>
                        <RouterLink :to="replaceText(action.href)"
                            v-if="action.isVisible &&  typeof action.callbackfunction == 'undefined'"
                            class="actions-onclick" v-bind:title='$t(action.title)'>
                            <em :class="action.icon"></em><br /><span class="text"> {{$t(action.title)}}</span>
                        </RouterLink>
                    </template>
                    <em class="fa fa-times close close-action" aria-hidden="true"></em>
                </span>
            </span>
        </span>
    </td>
</template>
<script>
export default {  
    props: {
        item: {
            type: Object,
            required: true       
        },
        listActions:{
            type: Array,
            required: true  
        },
         identityColumn: {
            type: String,
            required: true
        },
        index:null
    },
    methods:{
        ClickEvent: function (callbackfunction,item,index,event) {
            this.$emit('ButtonClick',callbackfunction, item,index,event);
        },
        replaceText: function(content){ 
            var vm=this; 
            var found = [], rxp = /{([^}]+)}/g, curMatch;
            while( curMatch = rxp.exec(content) ) {
                found.push(curMatch[1])
            }
            found.forEach((x) => {
                content = content.replace('{'+x+'}', `${ vm.item[x]}`);
            })
            return content;
        }     
    }
};
</script>
<style scoped>
    .action-list-box {
        opacity: 0;
        height: 0px;
        transition: opacity 1s;
    }
    .is-visible {
        display: inline-block;
        overflow: hidden !important;
        height: 46px;
    }
</style>