
<template>
    <section>
      
      <loader :is-visible="isLoading"></loader>   
 
    <TgList
        :listType="['List']"
        IsShowAction="true"
        ModuleName="ExpenseApproval"
        SubModuleCode="ExpenseApproval"
        IdentityColumn="EXPENSEID"
        :HeaderText="$t('ExpenseApproval')"
        :ListData="ExpenseApprovalData"
        :HeaderData="Headers"
        :HeaderButtons="listheaderbuttons"
        :TotalRecords="TotalRecords"      
        :SearchFields="leftSearchFields" 
        :RenderRowActionMethod="renderActions"
        :ListDataCallBackFunction="FetchData"       
         :SortExp="SortExp"
        :SortBy="SortBy"
        v-on:LeftsearchButtonClick="leftCommonSearch" 
        v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick" 
        v-on:ActionButtonClick="actionButtonClick"
        v-on:SortdataButtonClick="sortdata" 
        >   
        <template v-slot:slotdata="{data}">
            <template v-if="data.column.COLUMN_NAME == 'Status'">                
                <a class="inactive-disabled" >
                    <span v-bind:class="getStatusClass(data.row.Status, 'Status')" v-if="data.row.Status !='SentForCorrection'">
                        {{data.row[data.column.COLUMN_NAME]}}
                    </span>
                    <span v-bind:class="getStatusClass(data.row.Status, 'Status')" v-if="data.row.Status =='SentForCorrection'">
                        {{ $t('SentForCorrection') }}
                    </span>
                </a>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'Amount'">                                  
                    {{convertDecimalValueInFormat(data.row.Amount) +' '+data.row.ISO_CODE}}                   
            </template>           
        </template>          
    </TgList>
    <ExpenseComments v-if="showCommentModal" @CloseExpenseComments="CloseExpenseComments()" :access="access" v-bind:expenseid="expid" :CommentHeader="CommentHeader" :isSingleCommentforMultipleExpenses="isSingleCommentforMultipleExpenses" :statuscode="statuscode" @fetchdata="fetchdata()" :visibility="visibility"></ExpenseComments> -->
    <ExpenseApproverStatus v-if="showApproverStatusModal" @CloseExpenseApproverStatus="CloseExpenseApproverStatus()" v-bind:expenseid="expid"></ExpenseApproverStatus>
     <ExpenseAttachents v-if="showFileModal" @CloseExpenseAttachments='CloseExpenseAttachments' v-bind:expenseid="expid" v-bind:code="statuscode" ></ExpenseAttachents>
    </section>
</template>
<script>
   import ExpenseApprovalDataServices from "@/services/DataService";
    import Schema from "../ExpenseApproval/ExpenseApprovalSchema";
    import ExpenseComments from '../../../components/Common/Modal/ExpenseComments.vue';
    import ExpenseAttachents from '../../../components/Common/Modal/ExpenseAttachments.vue';
    import TgList from '../../../components/Common/TgList.vue'
    import ExpenseApproverStatus from '../../../components/Common/Modal/ApproverStatus.vue'
export default {
    components: {        
        ExpenseComments,
        ExpenseApproverStatus,
        ExpenseAttachents,
        TgList
      },
    data(){
        return{            
            isLoading:false,
            showFileModal:false,
            searchCondition: '',
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',            
            noRecord:false,
            CurrentPage: 1,
            TotalPages: 0,
            ExpenseApprovalData:null,
            ExpenseApprovaHistorylData:false,
            Headers:[{
                        "COLUMN_NAME": "Date",
                        "DATA_TYPE": "date",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Date",
                        "DISPLAY_ORDER": 1,
                        
                    },
                    {
                        "COLUMN_NAME": "expense_title",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Title",
                        "DISPLAY_ORDER": 2
                    },
                    {
                        "COLUMN_NAME": "UserName",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "UserName",
                        "DISPLAY_ORDER": 3
                    },
                    {
                        "COLUMN_NAME": "Category",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Category",
                        "DISPLAY_ORDER": 4
                    },
                    
                    {
                        "COLUMN_NAME": "Project",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Project_Name",
                        "DISPLAY_ORDER": 5
                    },
                    {
                        "COLUMN_NAME": "Amount",
                        "DATA_TYPE": "text",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Amount",
                        "DISPLAY_ORDER": 6,
                        settings:{
                            isInSlot : true
                        }
                    }                   
            ],         
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            ExpenseGetProjectList: null,
            ExpenseGetUserList:null,
            showCommentModal:false,
            CommentHeader:null,
            UserName:null,
            isSingleCommentforMultipleExpenses:null,  
            statuscode:'',
            showApproverStatusModal:false,
            IsShowAction:true,
            visibility:true,
            access:true
        }
    },
    created:function(){  
        debugger;               
        var vueObj=this;
        vueObj.FetchData();
        Schema.bindleftCommonSearchdropdown(vueObj,"Project","no","");
        Schema.bindleftCommonSearchdropdown(vueObj,"User","","EXPENSEAPPROVAL");
        Schema.bindleftCommonSearchdropdown(vueObj,"Show Data","","");
        vueObj.UserName=vueObj.GetUserInfo.Name;
    },
    updated: function () {        
        this.BindActionButtonEvent(); 
        this.SetScrolbar('dark-thick','', 'filterscroll_leftsearch');      
    },
    methods:{ 
        FetchData:function(){
            var vueObj = this;            
            vueObj.isLoading = true;
            const params = this.getRequestParams(this.sortBy,this.sortExp,this.pageSize,this.pageNumber,this.isPaged, this.searchcondition);
            params.pageSize = vueObj.PageSize;
            params.pageNumber = vueObj.PageNumber;
            params.searchCondition = vueObj.searchCondition;
            if (vueObj.PageNumber) { params.pageNumber = vueObj.PageNumber; }
            else { vueObj.PageNumber = params.pageNumber = 1; }
            if (typeof vueObj.isPaged != 'undefined') { params.isPaged = vueObj.isPaged; }
            else { vueObj.isPaged = params.isPaged = 1; }
            params.isPaged = vueObj.isPaged;
            params.sortBy = vueObj.SortBy;
            params.sortExp = vueObj.SortExp;
            var srchCondition = "";
            if (!vueObj.isBlank(vueObj.searchCondition)) { srchCondition = srchCondition + vueObj.searchCondition; }
            if (!vueObj.isBlank(vueObj.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!vueObj.isBlank(vueObj.searchCondition)) ? (" AND " + vueObj.conditionForInstantSearch) : vueObj.conditionForInstantSearch);
            }  
             if (srchCondition != "") {
                params.searchCondition = srchCondition; 
            }          
            ExpenseApprovalDataServices.ExpenseApprovalListing(params).then(function(response){      
                vueObj.isLoading = false;   
                if(response.data != null && response.data !='') 
                {
                    if(response.data.data.length>0)
                    {
                        vueObj.ExpenseApprovalData = [];
                        vueObj.ExpenseApprovalData = response.data.data;                        
                        vueObj.TotalRecords=response.data.data[0].TOTAL_RECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                    }
                    else{
                        vueObj.noRecord = true;
                        vueObj.ExpenseApprovalData = [];
                        vueObj.TotalRecords = 0
                    }
                }
                else{
                    vueObj.noRecord = true;
                    vueObj.ExpenseApprovalData = [];
                    vueObj.Headers = [];
                    vueObj.TotalRecords = 0
                }
                setTimeout(function () {vueObj.ResponsiveDataTable('tablelistingdata')}, 500); 
            });           
        },
        fetchdata(){            
             this.FetchData();
        },
        getRequestParams: function (sortBy,sortExp,pageSize,pageNumber,isPaged, searchcondition) {
            let params = {};
            if (pageNumber) { params["pageNumber"] = pageNumber; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {                
                let action = { ...actionItem };
                switch (action.title){
                    case 'View Attched Files':
                        action.isVisible= row.status_code=='ACT' || row.status_code=='EXP_SEND_FOR_CORRECTION';
                        break
                    case 'Comments':
                        action.isVisible=row.filescount != 0;
                        break
                    case 'Send For Correction': 
                        action.isVisible= row.commentscount != 0 || row.status_code != 'ACT';
                        break
                    case 'Approver Status':
                        action.isVisible= row.status_code=='ACT' || row.status_code=='EXP_SEND_FOR_CORRECTION';
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        leftCommonSearch: function (value) {            
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            if(this.ExpenseApprovaHistorylData){
                this.ViewHistoryList();
            }
            else{
                this.FetchData();
            }
        },
        actionButtonClick: function (callbackfunction, item) {               
            switch (callbackfunction) {
                case 'Edit': 
                    break;                  
                case 'openAddNewEvent': 
                    this.openAddNewEvent(item);
                    break;                 
                case 'SendForCorrection':
                    this.commonApproval('EXP_SEND_FOR_CORRECTION');
                    break;                           
                case 'ApproverStatus':  
                    this.ApproverStatus(item);               
                    break;
                case 'downloadAttachment':
                    this.OpenFileModal(item);
                    break;
            }
        },        
        CloseExpenseAttachments:function(){
            this.showFileModal=false;
        },
        sortdata: function (s) {            
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        commonHeaderButtonClick: function (event) {                                    
            switch (event.callbackfunction) {                
                case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                    break;
                case "SendForApprovalApprove":
                    this.commonApproval('EXP_APPROVED');
                    break;
                case "SendForApprovalReject":
                    this.commonApproval('EXP_REJECTED');
                    break;
                case "ViewHistory":
                    this.ViewHistoryList();
                    break;                                
            }
        },
        ViewHistoryList:function(){ 
            this.$router.push({ name: 'ExpenseHistory' });
            // const params = this.getRequestParams(this.sortBy,this.sortExp,this.pageSize,this.pageNumber,this.isPaged, this.searchcondition);
            // var vueObj = this;
            // vueObj.isLoading = true;
            // params.pageSize = vueObj.PageSize;
            // params.pageNumber = vueObj.PageNumber;
            // params.searchCondition = vueObj.searchCondition;
            // if (vueObj.PageNumber) { params.pageNumber = vueObj.PageNumber; }
            // else { vueObj.PageNumber = params.pageNumber = 1; }
            // if (typeof vueObj.isPaged != 'undefined') { params.isPaged = vueObj.isPaged; }
            // else { vueObj.isPaged = params.isPaged = 1; }
            // params.isPaged = vueObj.isPaged;
            // params.sortBy = vueObj.SortBy;
            // params.sortExp = vueObj.SortExp;
            // ExpenseApprovalDataServices.ExpenseApprovalHistory(params).then(function(response){                
            //     if(response.data != null){
            //         debugger;
            //         if(response.data.data.length>0)                    {
            //             vueObj.Headers = vueObj.HistoryHeaders;                                                               
            //             vueObj.ExpenseApprovalData = [];
            //             vueObj.ExpenseApprovalData = response.data.data;                        
            //             vueObj.TotalRecords=response.data.data[0].TOTALRECORDS;
            //             vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
            //             vueObj.CurrentPage = vueObj.PageNumber;
            //             vueObj.noRecord = false;
            //             vueObj.ExpenseApprovaHistorylData = true;
            //         }
            //         else{
            //             vueObj.noRecord = true;
            //             vueObj.ExpenseApprovalData = [];
            //             vueObj.TotalRecords = 0
            //         }
            //     }
            //     else{
            //         vueObj.noRecord = true;
            //         vueObj.ExpenseApprovalData = [];
            //         vueObj.Headers = [];
            //         vueObj.TotalRecords = 0
            //     }
            //     setTimeout(function () {vueObj.ResponsiveDataTable('tablelistingdata')}, 500);                    
            //     vueObj.isLoading = false;
            // });
        },
        CloseExpenseComments:function(){
           this.showCommentModal=false; 
        },
        commonApproval:function(statusForOut){
            debugger;
            var vueObj= this;
            vueObj.CommentHeader=`Comment for ${vueObj.UserName} Expense Approval`;
            vueObj.isSingleCommentforMultipleExpenses=1;            
            var expenseId = $("input.chkItems:checked");
            var ids = '';           
            var count = 0;
            for (var i = 0; i < expenseId.length; i++) {
                var id = $(expenseId[i]).val();
                var ruleid = 0;
                var approvalChainId = 0;
                var approvalGroupId = 0;
                if (ruleid == "") {
                    ruleid = 0;
                }
                if (i == 0) {
                    ids = id + "$" + ruleid + "$" + approvalChainId + "$" + approvalGroupId + "$";
                }
                else {
                    ids = ids + "," + id + "$" + ruleid + "$" + approvalChainId + "$" + approvalGroupId + "$";
                }
                count = count + 1;
            }
            vueObj.showCommentModal=true;
            vueObj.expid=ids;
            vueObj.statuscode=statusForOut;
            this.visibility=1;
            this.access=1;
        },
        openAddNewEvent:function(item){ 
            debugger;
            this.CommentHeader='Comment'; 
            this.isSingleCommentforMultipleExpenses=0;          
            this.showCommentModal=true;
            this.statuscode = null;
            this.expid = item.EXPENSEID;
            this.visibility=1;
            this.access=0;
        },
        OpenFileModal:function(item){ 
            this.showFileModal=true;            
            this.expid = item.EXPENSEID;
            this.statuscode = item.status_code;
        },
        ApproverStatus:function name(item) {                        
            this.showApproverStatusModal=true;     
            this.expid = item.EXPENSEID;       
        },
        CloseExpenseApproverStatus:function(){            
            this.showApproverStatusModal=false;
        }
    }
}
</script>