<script>
    export default {
        id: "list-header-buttons",
        props: {
            icons: { 
                type: Array, 
                required: true,
            },
            listType:{
                type: Array, 
                required: true,
            }
        },
        data: function () {
            return {
                selectedView: this.listType[0]
            };
        },
        methods: {
            ClickEvent: function (item) {
                this.$emit('ButtonClick', item);
            },
            ChangeViewEvent: function (item) {
                this.$emit('ChangeViewEvent', item);
            }
        }
    };
</script>
<template>
    <div class="theme-primary partition p-actions-expand">
        <span class="p-actions float-right">
          <template v-for="item in icons">
                <a v-bind:class="{'p-action-btn text-white ': true, 'disabled': item.isdisabled }" :id="item.id" :Key="item"
                   v-on:click="ClickEvent(item)" 
                   v-if="item.isvisible  &&  typeof item.callbackfunction != 'undefined' "
                   v-bind:title='$t(item.title)' 
                   href="javascript:;">
                   <em v-bind:class="'fa ' + item.iconClass"></em><br /><span>{{$t(item.title) }}</span>
                </a>
                <RouterLink :to="item.href" v-if="item.isvisible  &&  typeof item.callbackfunction == 'undefined'" :id="item.id" :Key="item"
                            v-bind:class="{'p-action-btn text-white dash ': true, 'disabled': item.isdisabled }"  
                            v-bind:title='$t(item.title)'
                            v-bind:checkPrivilege="(item.checkPrivilege ? 'yes':'')">
                    <em v-bind:class="'fa ' + item.iconClass"><br /></em><br /><span> {{$t(item.title)}}</span>
                </RouterLink>
            </template>
            <select v-if="listType.length > 1" id="ddlUserCurrentStatus" asp-custom-tabindex="1" name="ddlUserCurrentStatus" class="form-control customized_ddl m-2" style="width: 130px;" v-model="selectedView" v-on:change="ChangeViewEvent(selectedView)">
                <option :value="view" v-for="view in listType">{{view}} View</option>
            </select>
        </span>
        <span class="collapse-head-btns">
            <a href="javascript:;" class="collapse-btns"><em class="fa fa-angle-up text-white"></em></a>
        </span>
    </div>
</template>

