

<template> 
    <div>
        <div class="table-responsive table-fix-header">
        <table id="tablelistingdata" class="table table-bordered mt-0" style="width:100%"  aria-describedby="datatable">
            <thead class="thead-bg">
                <tr>
                     <th scope="col" style="width:50px !important;" class="text-center"><input id="chkAll" class="chkitems" value=""   type="checkbox" v-on:change="onChange()"></th>
                    <template v-for="(item,itemIndex) in Headers">
                         <th :key="itemIndex" scope="col" v-if="item.VISIBLE" style="text-transform:capitalize;" v-bind:id="item.COLUMN_NAME" v-bind:style="[(item.COLUMN_NAME=='status_name') ? {'width':'10%'} : {'width':''}]"
                         v-bind:class="{'text-center':subModuleCode=='CRM_BRANDS'&& item.COLUMN_NAME=='Social' || subModuleCode=='CRM_COMMUNICATION_MODES'&& item.COLUMN_NAME=='status'}">
                            <span @click="sortdata(item.COLUMN_NAME)"
                                  v-bind:class="{
                                                      'sort': item.SORTABLE,
                                                      'tb_headerSortUp': SortExp == 'ASC' && SortBy == item.COLUMN_NAME,
                                                      'tb_headerSortDown':
                                                        SortExp == 'DESC' && SortBy == item.COLUMN_NAME,
                                                    }"
                                  v-if="item.VISIBLE && item.SORTABLE">
                                {{$t(item.COLUMN_NAME,item.DISPLAY_NAME)}}
                            </span>
                            <span v-if="!item.SORTABLE">
                                {{$t(item.COLUMN_NAME,item.DISPLAY_NAME)}}
                            </span>
                        </th>
                    </template>
                     <th scope="col" v-if="IsShowAction == 'true'"   class="nonresize text-center"><span>{{ $t("Action") }}</span></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,index) in Data" :key="index" v-bind:class="item.additionalClass">
                    <td class="text-center"><input class="chkItems" v-bind:disabled="item.isCheckBoxDisabled" v-bind:value="item[identityColumn]" type="checkbox" v-on:change="onChange()"></td>
                    <template v-for="(key,keyIndex) in Headers">
                        <td :key="keyIndex" v-if="key.VISIBLE" v-bind:class="{'text-right':key.DATA_TYPE=='decimal'}">
                            <span v-if="(key.DATA_TYPE=='date' || key.DATA_TYPE=='datetime') && (key.DATA_TYPE !='decimal')">{{item[key.COLUMN_NAME] | formatDate}}</span>
                            <span v-if="(key.DATA_TYPE=='decimal')">{{item[key.COLUMN_NAME] | currency}}</span>
                            <span v-bind:title="item[key.COLUMN_NAME]" v-if="(key.DATA_TYPE!='date' && key.DATA_TYPE!='datetime' && key.DATA_TYPE!='decimal')">                                 
                                <RouterLink  id="viewLeadeContent_a" v-if="typeof(key.settings) != 'undefined' && key.settings != null 
                                && typeof(key.settings.clickEvent) != 'undefined' && key.settings.clickEvent != null && key.settings.isInSlot != true" 
                                 :to="typeof(item.rowDetailsUrl)!='undefined' ? item.rowDetailsUrl : ''" 
                                 @click.native="clickEventCheck(key,item)">
                                {{((typeof(key.settings.formatter) === 'function')?key.settings.formatter(item, key):TruncateStr(item[key.COLUMN_NAME],30))}}
                                </RouterLink>
                                <span v-if="typeof(key.settings) != 'undefined' && key.settings != null && (typeof(key.settings.clickEvent) === 'undefined' || key.settings.clickEvent == null) && key.settings.isInSlot != true" v-bind:title="item[key.COLUMN_NAME]">{{((typeof(key.settings.formatter) === 'function')?key.settings.formatter(item, key):TruncateStr(item[key.COLUMN_NAME],30))}}</span>
                                <span v-if="typeof(key.settings) === 'undefined' || key.settings == null" v-bind:title="item[key.COLUMN_NAME]">{{TruncateStr(item[key.COLUMN_NAME],30)}}</span>
                                <slot   v-if="typeof(key.settings) != 'undefined' && key.settings != null && typeof(key.settings.isInSlot) != 'undefined' && key.settings.isInSlot" name="slotdata" :data="{row:item,column:key}"></slot>
                            </span>
                        </td>
                    </template>
                    <RowActions v-if="IsShowAction == 'true'" :item="item" :index="index" v-on:ButtonClick="ButtonClick" :identityColumn="identityColumn" :listActions="renderRowActionMethod(item)"></RowActions>
                </tr>
                <tr v-if="noRecord">
                    <td class="text-center text-danger no-record" colspan="17">{{ $t("NoRecordfound") }}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <Pager  v-bind:numberofrecords="TotalRecords" :CurrentPageNumber="CurrentPageNumber" prev-text="Prev" first-text="First" last-text="Last" next-text="Next"  v-on:SetCurrentPage="pagerMethod"></Pager>
   
    </div>
    
</template>
<script>
import RowActions from '../../components/Common/RowActions.vue'
import Pager from '../../components/Common/Pager.vue'
import { RouterLink } from 'vue-router';
export default {
    props:{
        Data: {
            type: Array,
            required: true
        },
        Headers:null,
        IsShowAction:{
            type: String,
            required: true
        },
        noRecord: {
            type: Boolean,
            required: true,
        },
        totalrecords: {
            type: Number,
            required: true
        },
        renderRowActionMethod: {
            type:Function,
            required:true
        },
        identityColumn:{
            type: String,
            required:true
        },
        subModuleCode:{
            type:String,
            required:true
        },
        CurrentPageNumber:{
            type:Number
        },
        SortExp:null,
        SortBy:null,
    },
    components: {
        RowActions,
        Pager,
        RouterLink
    },
    watch: {
        totalrecords: function (value) {
            this.TotalRecords = value;
        }
    },
    data() {
        return { 
            showActions: this.NewArray(100),
            Actions: [],
            TotalRecords: 0
        }
    },
    methods: {
        clickEventCheck(key,item){
            ((typeof(key.settings.clickEvent) === 'function' && typeof(item.rowDetailsUrl)==='undefined')?key.settings.clickEvent(item, key):'')
        },
        NewArray(size) {
            var x = [];
            for (var i = 0; i < size; ++i) {
                x[i] = {
                    'QuickView': '',
                    'View': '',
                    'Edit': '',
                    'Notes': '',
                    'FollowUps': '',
                    'Reopen': '',
                    'ConvertLead': ''
                };
            }
            return x;
        },

            TruncateStr: function (str, len) {
                var convertstring = '' + str;
                if (convertstring != null)
                    if (convertstring.length <= len) return str;
                    else return convertstring.substring(0, len) + "...";
            },

            conditions(item, index) {
                var self = this;
                this.Actions.forEach(actionItem => {
                    switch (actionItem.title) {
                        case 'QuickView':
                        case 'View':
                        case 'Notes':
                            self.showActions[index][actionItem.title] = true;
                            break;
                        case 'Edit':
                            self.showActions[index][actionItem.title] = ((self.isBlank(item.existing_client_id) || self.isBlank(item.project_id)) && (item.status_id != 'Closed' && item.status_id != 'Cancelled'))
                            break;
                        case 'FollowUps':
                            self.showActions[index][actionItem.title] = item.status_id != 'Closed';
                            break;
                        case 'Reopen':
                            self.showActions[index][actionItem.title] = item.status_id == 'Closed';
                            break;
                        case 'ConvertLead':
                            self.showActions[index][actionItem.title] = ((self.isBlank(item.existing_client_id) || self.isBlank(item.project_id)) && (item.status_id != 'Closed' && item.status_id != 'Inactive' && item.status_id != 'Draft' && item.status_id != 'Cancelled'))
                            break;
                    }
                })
                this.Actions = this.listActions;
                return self.showActions;
            },

            isCRMShowStausDrop: function (status, moduleName) {
                if (status == 'Draft' || status == 'Partially Received' || status == 'Approved' || status == 'Rejected' || status == 'Received' || status == 'Partially Approved' || status == 'Partially Rejected' || status == 'Approval Pending' || status == 'In Stock' || status == 'Cancelled' || (status == "Closed" && moduleName != "CRM_LEADS")) {
                    return false;
                }
                return true;
            },
            getStatusClass: function (status, columnName) {
                var returnClass = ''
                if (columnName == 'status_id' || columnName == 'status_name' || columnName == 'gin_status') {
                    switch (status) {
                        case 'Active': case 'Approved':
                            returnClass = 'px-2 bg-success text-center text-white d-block';
                            break;
                        case 'Inactive': case 'Closed': case 'Cancelled': case 'Rejected':
                            returnClass = 'px-2 bg-danger text-center text-white d-block';
                            break;

                        case 'Approval Pending': case 'Partially Approved':
                            returnClass = 'px-2 bg-info text-center text-white d-block';
                            break;
                        case 'Received': case 'Partially Received':
                            returnClass = 'px-2 bg-secondary text-white text-center d-block';
                            break;
                        case 'Draft':
                            returnClass = 'px-2 bg-warning text-center text-white d-block';
                            break;
                        case 'No Gin':
                            returnClass = 'px-2 bg-primary text-center text-white d-block';
                            break;

                        default:
                          
                            returnClass = 'px-2 bg-secondary text-center text-white d-block';
                    }
                    return returnClass;
                }
            },

            sortdata: function (s) {
                this.$emit('sortdata', s);
            },
            pagerMethod: function (s) {
                debugger; // eslint-disable-line no-debugger
                this.$emit('PagerMethod', s);
            },
            isBlank: function (str) {
                return (!str || /^\s*$/.test(str));
            },
            ButtonClick: function (callbackfunction, item, index, event) {
                this.$emit('ButtonClick', callbackfunction, item, index, event);
            },
        onChange: function () {
         
            var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
            if (checkCount > 0) {
                $('#Sendbulkmail').removeClass('disabled');
                $('#CloseLeadId').removeClass('disabled');
                $('#ChangeOwnerId').removeClass('disabled');
                $('#AdditionalOwnerId').removeClass('disabled');
                $('#Inactive').removeClass('disabled');
                $('#Active').removeClass('disabled');
                $('#Reject').removeClass('disabled');
                $('#Approve').removeClass('disabled');
            }
            else {
                $('#Sendbulkmail').addClass('disabled');
                $('#CloseLeadId').addClass('disabled');
                $('#ChangeOwnerId').addClass('disabled');
                $('#AdditionalOwnerId').addClass('disabled');
                $('#Inactive').addClass('disabled');
                $('#Active').addClass('disabled');
                $('#Reject').addClass('disabled');
                $('#Approve').addClass('disabled');
            }
        },
        }
    }
</script>
<style>
.arrow-solid-left::before {
    content: "\f0dd";
    transform: rotate(89deg);
    display: inline-block;
    transition: transform .2s;
    transform-origin: right;
    position: absolute;
    right: 3px;
    top: 46%;
}
.right-content-expand .arrow-solid-left::before {
    transform: rotate(271deg) !important;
    transition: transform .2s;
    transform-origin: left;
    right: 1px;
}
</style>
