<script>
import LeadDataService from "../../services/DataService";
export default {
  props: {
    fieldInfo: {
      type: Object,
      required: true,
    },
    odnumber:null,
    selectedQuotationType:null
  },
  data: function () {
    return {
      isLoading: false,
      language:'Robin'
    };
  }, 
  created(){
   if(this.fieldInfo.select_options==''){
    this.fieldInfo.select_options=[]
   }
  },
  updated(){
console.log(this.fieldInfo);
  },
  methods: {
    getRuleObj(){
      var initialRule={}
      if(this.selectedQuotationType != 1 && this.fieldInfo.name=='client_id'){
        initialRule.required=false
      }else{
        initialRule.required=this.fieldInfo.is_required;
      }
         return initialRule;      
    },
    setSelectedValue:function(item, val, isFor, type){
            this.$emit('setSelectedValue',item, val, isFor, type);
          },  
    onSearchDDL: function(search, objItem, isFor){
            this.$emit('onSearchDDL',search, objItem, isFor);
          },        
    onSearch: async function onSearch(search, selctedValue) {
      var vm = this;
      vm.isLoading=true;
      if (typeof selctedValue == "undefined") {
        selctedValue = 0;
      }
      var param =
        "id=&moduleName=CRM&type=" +
        vm.fieldInfo.field_code +
        "&search=" +
        escape(search);
      if (vm.fieldInfo.dependent_type == "fill") {
        param += "&refId=" + selctedValue;
      }
      await LeadDataService.getLeaOwnerBySearch(param).then(function (
        response
      ) {
        vm.isLoading=false;
        var json = response.data;
        if (json != null && json!='') {
          vm.fieldInfo.select_options = json.DATA;         
        }
      });
    },   
  },
};
</script> 
<template>
  <Field class="w-100"
    :name="GetFormSchemaFieldName(fieldInfo.name,fieldInfo.display_name)"
    :rules="{'required':fieldInfo.is_required}"
    v-slot="{errors,changed}">   
    <Multiselect  
      v-model="fieldInfo.value"
      :placeholder="GetFieldPlaceHolder(fieldInfo.label,fieldInfo.display_name,'select')"   
      v-bind:disabled="(fieldInfo.name == 'account_id' && $route.params.reqFromId && $route.params.reqFromId!= '0') || (odnumber == 'Drafted')"
      :filter-results="false"
      :object="true"
      label="name"
      :searchable="true"
       track-by="value"
       :show-labels="false"
      :show-no-results="false"
      :internal-search="false"
      :loading="isLoading"
      :clear-on-select="false" :close-on-select="true"
      v-bind:class="{'w-100':true,'is-invalid':errors[0]}" 
      @select="setSelectedValue(fieldInfo,$event)"
      @update:modelValue="changed"
      @search-change="onSearchDDL($event,fieldInfo)"
      :options="fieldInfo.select_options"
    >
    </Multiselect>
   <span   :name="fieldInfo.name" class="invalid-feedback d-block">{{errors[0]}}</span>  
  </Field>
</template>

<style>
.multiselect {min-width: 200px; border-radius: 0px!important;color: #303030!important; border-color: #abb5bf!important; 
  min-height: 28px;font-size: 14px;}
.multiselect-dropdown > .multiselect-no-options{display: none;}
.multiselect__select:before {color: #000; border-color: #0e0e0e transparent transparent;}
.multiselect.is-invalid div.multiselect__tags{padding-top: 0px;}
.multiselect.is-invalid .multiselect__input{min-height: 32px; padding-left: 0px;}
.multiselect__tags {min-height: 36px; padding: 8px 40px 0 8px; border-radius: 0px; border: 1px solid #abb5bf;}
.multiselect__single {font-size: 14px;margin-bottom: 6px;}
.multiselect__placeholder {color:#515058; margin-bottom: 6px; padding-top: 0px;}
.multiselect__input{margin-bottom:1px; min-height: auto; line-height:normal; font-size: 14px;}
</style>