<script>
    import Pager from './Pager.vue'
    import RowActions from './RowActions.vue'
    export default {
        components:{
            Pager,
            RowActions
        },
        props:{
            data:{
                type: Array,
                required: true
            },
            columns:  {
                type: Array,
                required: true
            },
            totalrecords: {
                type: Number,
                required: true,
                deep: true
            },
            renderRowActionMethod: {
                type:Function,
                required:true
            },
            IsShowAction:{
                type: String,
                required: true
            },
            identityColumn:{
                type: String,
                required:true
            },
            subModuleCode:{
                type: String,
                required:true
            },
            customcard:{
                type: Boolean,
                default: false
            }
        },
        watch: {
            totalrecords: function (value) {
                this.TotalRecords = value;
            }
        },
        data() {
            return {
                TotalRecords: 0
            }
        },
        methods: {
            TruncateStr: function (str, len) {
                var convertstring = '' + str;
                if (convertstring != null)
                    if (convertstring.length <= len) return str;
                    else return convertstring.substring(0, len) + "...";
            },
            pagerMethod: function (s) {
                this.$emit('PagerMethod', s);
            },
            isShowColumn: function(row, key){
                if((!row[key.COLUMN_NAME]) || row[key.COLUMN_NAME] == null || row[key.COLUMN_NAME] == ''){
                    return false;
                } else if (typeof(key.settings) !== 'undefined' && key.settings != null &&  typeof(key.settings.isForTag) !== 'undefined' &&  key.settings.isForTag == true){
                    return false;
                }
                return true;
            },
            tagColor: function(row, key){
                if(row[key.COLUMN_NAME].toLowerCase().includes("active") || row[key.COLUMN_NAME].toLowerCase().includes("open") || row[key.COLUMN_NAME].toLowerCase().includes("approve")){
                    return "btn-success";
                } else if(row[key.COLUMN_NAME].toLowerCase().includes("inactive") || row[key.COLUMN_NAME].toLowerCase().includes("delete") || row[key.COLUMN_NAME].toLowerCase().includes("cancel") || row[key.COLUMN_NAME].toLowerCase().includes("reject") || row[key.COLUMN_NAME].toLowerCase().includes("close")){
                    return "bg-danger"
                } else if(row[key.COLUMN_NAME].toLowerCase().includes("draft") || row[key.COLUMN_NAME].toLowerCase().includes("partial")){
                    return "bg-warning";
                }
                return 'bg-secondary';
            },
            defaultIconsForColumn: function(key) {
                if(typeof(key.settings) != 'undefined' && key.settings != null && typeof(key.settings.icon) != 'undefined'){
                    return key.settings.icon;
                } else if (key.COLUMN_NAME.toLowerCase().includes("email")) {
                    return "fa-envelope";
                } else if (key.COLUMN_NAME.toLowerCase().includes("subject")) {
                    return "fa-tasks";
                } else if (key.COLUMN_NAME.toLowerCase().includes("description")) {
                    return "fa-file";
                } else if (key.COLUMN_NAME.toLowerCase().includes("date") || key.COLUMN_NAME.toLowerCase().includes("created_at") || key.COLUMN_NAME.toLowerCase().includes("modified_at")) {
                    return "fa-calendar";
                } else if (key.COLUMN_NAME.toLowerCase().includes("user") || key.COLUMN_NAME.toLowerCase().includes("created_by") || key.COLUMN_NAME.toLowerCase().includes("modified_by")) {
                    return "fa-user";
                } else if (key.COLUMN_NAME.toLowerCase().includes("users")) {
                    return "fa-user";
                } else if (key.COLUMN_NAME.toLowerCase().includes("client")) {
                    return "fa-user-o";
                } else if (key.COLUMN_NAME.toLowerCase().includes("contact")) {
                    return "fa-phone-square";
                } else if (key.COLUMN_NAME.toLowerCase().includes("country")) {
                    return "fa-globe";
                } else if (key.COLUMN_NAME.toLowerCase().includes("count") || key.COLUMN_NAME.toLowerCase().includes("number") || key.COLUMN_NAME.toLowerCase().includes("cost")) {
                    return "fa-money";
                } else if (key.COLUMN_NAME.toLowerCase().includes("address") || key.COLUMN_NAME.toLowerCase().includes("city")) {
                    return "fa-address-card";
                } else {
                    return "fa-exclamation-triangle";
                }
            },  
            ButtonClick: function (callbackfunction, item, index, event) {
                this.$emit('ButtonClick', callbackfunction, item, index, event);
            },
            onChangeEventOfCheckbox: function(e, item){
                this.$emit('EventOfCheckbox', e, item);
            },
            clickEventCheck(columns,row){
                ((typeof(columns[0].settings.clickEvent)  === 'function')?columns[0].settings.clickEvent(row, columns[0]):'')                
            },
            clickEventCheck2(key,row){
                ((typeof(key.settings.clickEvent) === 'function')?key.settings.clickEvent(row, key):'')
            }
        },
    }
</script>
<template>
     <div>
    <div class="row card-view bg-border custom-box-width">
        <div class="col-md-12 col-lg-6 col-xl-3 mb-3 fsmaddresscontrol" v-for="(row, index) in data">
            <div v-if="!customcard" v-bind:class="{'card-box':true}" style="border-width: 5px 2px 2px; border-style: solid; border-color: rgb(153 153 153); border-image: initial;">
                <div class="mb-2">
                    <div class="custom-control custom-checkbox">
                        <input class="chkItems" v-model="row.isSelected" v-on:change="onChangeEventOfCheckbox($event, item)" v-bind:disabled="row.isCheckBoxDisabled" v-bind:value="row[identityColumn]" type="checkbox" />
                    </div>
                    <span v-if="(columns[0].DATA_TYPE=='date' || columns[0].DATA_TYPE=='datetime') && (columns[0].DATA_TYPE !='decimal')">{{$filters.formatDate(row[columns[0].COLUMN_NAME])}}</span>
                    <span v-if="(columns[0].DATA_TYPE=='decimal')">{{$filters.currency(row[columns[0].COLUMN_NAME])}}</span>
                    <span v-bind:title="row[columns[0].COLUMN_NAME]" v-if="(columns[0].DATA_TYPE!='date' && columns[0].DATA_TYPE!='datetime' && columns[0].DATA_TYPE!='decimal')">      
                        <a  id="viewLeadeContent_a" v-if="typeof(columns[0].settings) != 'undefined' && columns[0].settings != null && typeof(columns[0].settings.clickEvent) != 'undefined' && columns[0].settings.clickEvent != null && columns[0].settings.isInSlot != true" href="javascript:;" 
                        v-on:click="clickEventCheck(columns,row)">{{((typeof(columns[0].settings.formatter) === 'function')?columns[0].settings.formatter(row, columns[0]):TruncateStr(row[columns[0].COLUMN_NAME],30))}}</a>
                        <span v-if="typeof(columns[0].settings) != 'undefined' && columns[0].settings != null && (typeof(columns[0].settings.clickEvent) === 'undefined' || columns[0].settings.clickEvent == null) && columns[0].settings.isInSlot != true" v-bind:title="row[columns[0].COLUMN_NAME]">{{((typeof(columns[0].settings.formatter) === 'function')?columns[0].settings.formatter(row, columns[0]):TruncateStr(row[columns[0].COLUMN_NAME],30))}}</span>
                        <span v-if="typeof(columns[0].settings) === 'undefined' || columns[0].settings == null" v-bind:title="row[columns[0].COLUMN_NAME]">{{TruncateStr(row[columns[0].COLUMN_NAME],30)}}</span>
                        <slot   v-if="typeof(columns[0].settings) != 'undefined' && columns[0].settings != null && typeof(columns[0].settings.isInSlot) != 'undefined' && columns[0].settings.isInSlot == true" name="slotdata" :data="{row:row,column:columns[0]}"></slot>
                    </span>
                </div>
                <div class="card-detail-main filterscrollForCardView">
                    <div class="card-detail" v-for="(key, index) in columns" v-show="isShowColumn(row,key) && index > 0" :key="key">
                        <i :class="'fa '+ defaultIconsForColumn(key)" :data-name="key.COLUMN_NAME" aria-hidden="true"></i>
                        <!-- <i class="fa fa-exclamation-triangle" aria-hidden="true" v-if="typeof(key.settings) === 'undefined' || key.settings == null || (typeof(key.settings) != 'undefined' && key.settings != null && typeof(key.settings.icon) == 'undefined')"></i> -->
                        <span v-if="(key.DATA_TYPE=='date' || key.DATA_TYPE=='datetime') && (key.DATA_TYPE !='decimal')">{{$filters.formatDate(row[key.COLUMN_NAME])}}</span>
                        <span v-if="(key.DATA_TYPE=='decimal')">{{$filters.currency(row[key.COLUMN_NAME])}}</span>
                        <span v-bind:title="row[key.COLUMN_NAME]" v-if="(key.DATA_TYPE!='date' && key.DATA_TYPE!='datetime' && key.DATA_TYPE!='decimal')">      
                            <a  id="viewLeadeContent_a" 
                            v-if="typeof(key.settings) != 'undefined' && key.settings != null && typeof(key.settings.clickEvent) != 'undefined' && key.settings.clickEvent != null && key.settings.isInSlot != true" href="javascript:;" 
                            v-on:click="clickEventCheck2(key,row)">{{((typeof(key.settings.formatter) === 'function')?key.settings.formatter(row, key):TruncateStr(row[key.COLUMN_NAME],30))}}</a>
                            <span v-if="typeof(key.settings) != 'undefined' && key.settings != null && (typeof(key.settings.clickEvent) === 'undefined' || key.settings.clickEvent == null) && key.settings.isInSlot != true" v-bind:title="row[key.COLUMN_NAME]">{{((typeof(key.settings.formatter) === 'function')?key.settings.formatter(row, key):TruncateStr(row[key.COLUMN_NAME],30))}}</span>
                            <span v-if="typeof(key.settings) === 'undefined' || key.settings == null" v-bind:title="row[key.COLUMN_NAME]">{{TruncateStr(row[key.COLUMN_NAME],30)}}</span>
                            <slot   v-if="typeof(key.settings) != 'undefined' && key.settings != null && typeof(key.settings.isInSlot) != 'undefined' && key.settings.isInSlot == true" name="slotdata" :data="{row:row,column:key}"></slot>
                        </span> 
                    </div>
                </div>
                <div class="bottom-action" style="background:none !important;">
                    <template v-for="key in columns">
                        <div :class="'px-2 text-center text-white d-block ' + tagColor(row, key)" v-if="typeof(key.settings) !== 'undefined' && key.settings != null &&  typeof(key.settings.isForTag) !== 'undefined' &&  key.settings.isForTag == true">
                            {{row[key.COLUMN_NAME]}}
                        </div>
                    </template>
                    <div class="card-action my-2">
                        <RowActions v-if="IsShowAction == 'true'" :item="row" :index="index" @button-click="ButtonClick" :identityColumn="identityColumn" :listActions="renderRowActionMethod(row)" listType="Card"></RowActions> 
                    </div>
                </div>
            </div>
            <template v-if="customcard">
                <slot name="customcard" :data="{row:row,columnlist:columns, onchangecheckbox:onChangeEventOfCheckbox }"></slot>
            </template>
        </div>
    </div>
    <Pager v-bind:numberofrecords="totalrecords" prev-text="Prev" first-text="First" last-text="Last" next-text="Next" @set-current-page="pagerMethod"></Pager>
    </div>
</template>