<template>
<div id="listing">
    <div class="col-md-12 p-0">
        <form id="frmReport" method="post" target="iReport" asp-controller="ReportCommon" asp-action="ReportMain">
            <input type="hidden" id="projectIds" name="projectIds" value="" />
            <input type="hidden" id="categoryIds" name="categoryIds" value="" />
            <input type="hidden" id="tripIds" name="tripIds" value="" />
            <input type="hidden" id="userIds" name="userIds" value="" />
            <input type="hidden" id="approverIds" name="approverIds" value="" />
            <input type="hidden" id="statusIds" name="statusIds" value="" />
            <input type="hidden" id="hdnmodulename" name="moduleName" value="Expense" />
            <input type="hidden" id="reportName" name="reportName" value="expensereport" />
            <div class="row">
                <div class="col-lg-4">
                    <div class="form-group" >
                            <label>{{ $t('Project') }}:</label>
                            <Multiselect v-model="projectId" mode="tags" placeholder="Choose your Project" :close-on-select="true"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="projects" track-by="value" :object="true"   label="label"/> 
                                                           
                        </div>
                    </div>
             
                <div class="col-lg-4">
                    <div class="form-group">
                            <label>{{ $t('Category') }}:</label>
                            <Multiselect v-model="categoryId" mode="tags" placeholder="Choose your Category" :close-on-select="true"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="categories" track-by="value"  label="label" :object="true"  />
                                
                        </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                            <label>{{ $t('Trip') }}:</label>
                            <Multiselect v-model="TripId" mode="tags" placeholder="Choose your Trip" :close-on-select="true"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="trips" track-by="value"  label="label" :object="true"  />
                                
                        </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                            <label>{{ $t('User') }}:</label>
                            <Multiselect v-model="UserId" mode="tags" placeholder="Choose your Trip" :close-on-select="true"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="ProjectUser" track-by="value"  label="label" :object="true"  />
                                
                        </div>
                </div>
               
                    <div class="col-lg-4">
                        <div class="form-group" v-if="UserType=='CA'">
                            <label>Approver:</label>
                            <Multiselect v-model="ApproverId" mode="tags" placeholder="Choose your Trip" :close-on-select="true"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="approve" track-by="value"  label="label" :object="true"  />
                          </div>
                    </div>
        
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>Status:</label>
                        <Multiselect v-model="StatusId" mode="tags" placeholder="Choose your Trip" :close-on-select="true"
                                :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0" :searchable="true" 
                                :options="status" track-by="value"  label="label" :object="true"  />
                       </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>Title:</label>
                        <input type="text" name="ExpenseTitle" id="ExpenseTitle"  class = "form-control" />
                        <span data-valmsg-for="ExpenseTitle" data-valmsg-replace="true" class="valid-feedback">
                        </span>
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>Date:</label>
                        <div class="row">
                            <div class="col-lg-6">
                                <div class="input-group mb-2">
                                   
                                        <v-date-picker  :minute-increment="5" class="input-group" 
                                        v-model="datepicker2" :attributes='attrs'>
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control date' : true}" :value="inputValue"
                                                    v-on="inputEvents"  placeholder="From"  />
                                                <div @click="togglePopover()" class="input-group-append">
                                                    <span class="input-group-text">
                                                        <em class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>
                                   
                                </div>
                            </div>
                            <div class="col-lg-6">
                                <div class="input-group mb-2">
                                    <v-date-picker  :minute-increment="5" class="input-group" 
                                        v-model="endDate" :attributes='attrs'>
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control date' : true}" :value="inputValue"
                                                    v-on="inputEvents"  placeholder="To"  />
                                                <div @click="togglePopover()" class="input-group-append">
                                                    <span class="input-group-text">
                                                        <em class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>
                                   
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>GroupBy:</label>
                        <div class="pl-0 border-0">
                            <div class="custom-control custom-radio d-inline-block">
                                <input data-change-id="no" class="form-check-input" name="groupBy" id="Project" checked="checked" type="radio" value="Project">
                                <label class="form-check-label" for="Project">
                               {{ $t('Project')}}
                                </label>
                            </div>
                            <div class="custom-control custom-radio d-inline-block">
                                <input data-change-id="no" class="form-check-input" name="groupBy" id="Category" type="radio" value="Category">
                                <label class="form-check-label" for="Category">
                                    {{ $t('Category')}}
                                </label>
                            </div>
                            <div class="custom-control custom-radio d-inline-block">
                                <input data-change-id="no" class="form-check-input" name="groupBy" id="Trip" type="radio" value="Trip">
                                <label class="form-check-label" for="Trip">
                                    {{ $t('Trip')}}
                                </label>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row flex-row-reverse mt-2">
                <div class="col-lg-6 text-right">
                    <a href="javascript:;" id="aSearch" title="@Resources.Search" name="aSearch" class="btn btn-success formbtn text-white"><i class="fa fa-search pr-2"></i>Search</a>
                    <a href="javascript:;" title="@Resources.Clear" id="aClear" class="btn btn-danger formbtn widthhalf btnCancel"><i class="fa fa-close pr-2"></i>Clear</a>
                </div>
            </div>
        </form>
    
    </div>
    <div class="row placeholder">
        <div class="col-md-12 p-0">
            <div id="dvMainReportPanel" style="box-sizing: border-box; -webkit-overflow-scrolling:touch!important; text-align: center !important; width: 100% !important;display:none;">
                <ReportViewer v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath"></ReportViewer>
        </div>
    </div>
</div>
</div>
</template>
<script>
import DataService from "@/services/DataService";
export default {

    data: function () {
        return {
            trips:[],
            categories:[],
            projects:[],
            status:[],
          ModuleName:'Expense',
          isexpenseReport:true,
            projectId:null,
            categoryId:null,
            TripId:null,
            approve:[],
            approverId:null,
            StatusId:null,
            attrs: [
                {
                    key: 'today',
                    bar: true,
                    dates: new Date(),
                },
            ],
            reportPath:null,
            parameter:[]
        }
    },
    created: function(){
        debugger;
        this.GetProjectByCompanyIdAndUserId();
        this.GetStatusList("Expense", "en");
        this.UserType=this.GetUserInfo.UserType
        this.ExpenseGetApproverListing();
        this.GetAllTripsByCompanyId();
    },
    methods: {
        onSearch: function () {
         var vm=this;
            var reportModel={
                reportName:'expensereport',
                moduleName:'Expense',
                projectIds:vm.projectId.toString(),
                categoryIds:vm.categoryId.toString(),
                status:vm.StatusId.toString(),
                trips:vm.TripId.toString(),
                dateFrom:vm.dtFrom,
                dateTo:vm.$options.filters.formatDate(vm.dtTO),
                expenseOverLimit:vm.isexpenseOverLimit
            }  
            var objectPostString = JSON.stringify(reportModel);     
            DataService.ReportMain(objectPostString).then(response => {                  
                     vm.reportPath=response.data.reportpath;
                     vm.parameter=response.data.parameters;
            });
        },
        GetProjectByCompanyIdAndUserId() {
          
          var vm = this;
          DataService.GetProjectByCompanyIdAndUserId().then(function (response) {
          vm.projects = response.data.map((item) => {
          return { value: item.projectId, label: item.projectName };
        });
              console.log(vm.projects);
              vm.GetAllCategoriesByCompanyId();
          });
      },
      GetAllCategoriesByCompanyId() {
       var vm = this;
          DataService.GetAllCategoriesByCompanyId().then(function (response) {
              vm.categories =  response.data.map((item) => {
          return { value: item.expensE_CATEGORY_ID, label: item.categorY_NAME };
        });
      });
      },
      ExpenseGetApproverListing:function(){
            let params = {};
            var vm=this;
            params["IsEncrptedId"] = '';
            params["moduleName"] = 'Project';
            DataService.ExpenseGetUserListing(params).then(function (response) {
                vm.approve =  response.data.map((item) => {
          return { value: item.USER_ID, label: item.USERNAME };
        });
            });
    },
    
    GetStatusList: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
           
            DataService.GetStatusList(url).then(function (response) {  
                 vm.status = response.data.map((item) => {
          return { value: item.STATUS_ID, label: item.STATUS };
        });
                          
            });
    },
      GetAllTripsByCompanyId() {
       var vm = this;
       DataService.GettTripListingddl().then(function (response) {
              vm.trips =  response.data.map((item) => {
          return { value: item.TRIP_ID, label: item.TRIP_NAME };
        });
      });
      },
    }

}
</script>