
<template>
    <div>
    <div v-if="(typeof(SearchPosition) === 'undefined' || SearchPosition == 'left') && SearchFields" class="left-menu-tab" id="crmleftsearch">
        <leftsearch :header="HeaderText"
                    v-bind:fields="SearchFields"
                    v-on:SetLeftSearch="leftCommonSearch"
                    :modulename="ModuleName">
        </leftsearch>
    </div>
    <div id="crmrightsearch" :class="{'right-content': ((typeof(SearchPosition) === 'undefined' || SearchPosition == 'left') && SearchFields)}">
        <div class="col-md-12 p-0" v-if="HeaderButtons">
            <list-header-buttonNew  v-if="SubModuleCode=='CRM_LEADS'" v-bind:icons="HeaderButtons"  
           v-on:RefreshManageView="RefreshManageView"  v-on:ButtonClick="commonHeaderButtonClick" :modulename="ModuleName" :submodulecode="SubModuleCode"></list-header-buttonNew>
             <list-header-buttons v-else v-bind:icons="HeaderButtons" v-on:ButtonClick="commonHeaderButtonClick"
             v-on:ChangeViewEvent="ChangeViewEvent" :listType="listType"></list-header-buttons>
           
        </div>
        <div class="py-2 d-inline-block">
            <template v-for="lgnd in LegendArray">
                <span :class="lgnd.class"></span>{{ lgnd.text }}
            </template>
            
        </div>
        <div class="listing">
            <div class="expand-list" v-if="(typeof(SearchPosition) === 'undefined' || SearchPosition == 'left')">
                <a href="javascript:;" @click="ExpandCollapseList" v-bind:class="{'expand-list-btn justify-content-center align-items-center': true, 'filter-selected':IsFilterApplied}">
                    <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                </a>
            </div>
            <div class="text-center text-danger" v-if="ErrorText">{{ErrorText}}</div>
            <DataTable v-if="ListData && selectedView == 'List'"
                        :Data="ListData"
                        :noRecord="noRecord"
                        :Headers="HeaderData"
                        :SortExp="SortExp"
                        :SortBy="SortBy"
                        :totalrecords="TotalRecordsForPaging"
                        :IsShowAction="IsShowAction"
                        v-on:sortdata="sortdata"
                        v-on:ButtonClick="actionButtonClick"
                        v-on:PagerMethod="pagerMethod"
                        :renderRowActionMethod="RenderRowActionMethod"
                        :identityColumn="IdentityColumn"
                        :subModuleCode="SubModuleCode"
                        :CurrentPageNumber="CurrentPageNumber"                      
                        >
                        <template v-slot:slotdata="{data}">
                            <slot name="slotdata" :data="{row:data.row,column:data.column}"></slot>
                        </template>
            </DataTable>
            <CardView v-if="ListData && selectedView == 'Card'" 
                :data="ListData" :columns="HeaderData" 
                :totalrecords="TotalRecordsForPaging"
                :customcard="IsShowCustomCard"
                v-on:PagerMethod="pagerMethod" 
                v-on:ButtonClick="actionButtonClick"
                v-on:EventOfCheckbox="onChangeEventOfCheckbox"
                :IsShowAction="IsShowAction" 
                :identityColumn="IdentityColumn"
                :subModuleCode="SubModuleCode"
                :renderRowActionMethod="RenderRowActionMethod">
                <template v-slot:slotdata="{data}">
                    <slot name="slotdata" :data="{row:data.row,column:data.column,listtype:selectedView}"></slot>
                </template>
                <template v-slot:customcard="{ data }">
                    <slot name="customcard" :data="{row:data.row,columnlist:data.columnlist,onchangecheckbox:data.onchangecheckbox,listtype:selectedView}"></slot>
                </template>
            </CardView>
            <KanbanView v-if="ListData && selectedView == 'Kanban'" 
                :data="ListData" :columns="HeaderData" 
                :totalrecords="TotalRecordsForPaging"
                :customcard="IsShowCustomCard"
                v-on:PagerMethod="pagerMethod" 
                v-on:ButtonClick="actionButtonClick"
                v-on:EventOfCheckbox="onChangeEventOfCheckbox"
                :IsShowAction="IsShowAction" 
                :identityColumn="IdentityColumn"
                :subModuleCode="SubModuleCode"
                :renderRowActionMethod="RenderRowActionMethod">
                <template v-slot:slotdata="{data}">
                    <slot name="slotdata" :data="{row:data.row,column:data.column,listtype:selectedView}"></slot>
                </template>
                <template v-slot:customcard="{ data }">
                    <slot name="customcard" :data="{row:data.row,columnlist:data.columnlist,onchangecheckbox:data.onchangecheckbox,listtype:selectedView}"></slot>
                </template>
            </KanbanView>
        </div>
    </div>
</div>
</template>
<script>
import DataTable from '../../components/Common/DataTable.vue';
import leftsearch from '../../components/Common/left-search.vue';
import ListHeaderButtons from '../../components/Common/list-header-buttons.vue'
import ListHeaderButtonsNew from '../../components/Common/list-header-buttonNew.vue'
import CardView from './CardView.vue' // Card Vuew Component used for card view.
import KanbanView from './KanbanView.vue'
import { useSlots } from 'vue'
export default {
    props:{
        listType:{
            type: Array, // ListType property contains  array  with three values -  ['List', 'Card', 'Kanban']
            default: ["List"]
        },
        ListData:  null, 
        HeaderText:{
            type: String,
            required: true
        },
        HeaderData: null,
        IsShowAction: {
            type: String,
            required: true
        },
        SearchPosition:{
            type: String,
            required: false
        },
        SearchFields: {
            type: Array,
            required: true
        },
        LegendArray: null,
        HeaderButtons: {
            type: Array,
            required: true
        },
        ModuleName: {
            type: String,
            required: true
        },
        SubModuleCode: {
            type: String,
            required: true
        },
        IdentityColumn: {
            type: String,
            required: true
        },
        RenderRowActionMethod: {
            type: Function,
            required: true
        },
        TotalRecords: {
            type: Number,
            required: true
        },
        CurrentPageNumber:{
            type:Number
        },
        IsFilterApplied:null,
        SortExp:null,
        SortBy:null,
        KanbanDataCallBackFunction: {
            type: Function,
            default: null
        },
        widgets:{
            type: Array,
            default: []
        }
    },
    components: {
        leftsearch,
        DataTable,
        CardView,
        KanbanView,
        "list-header-buttons": ListHeaderButtons,
        "list-header-buttonNew":ListHeaderButtonsNew
    },
    data() {
        return {
            ErrorText : "",
            TotalRecordsForPaging: 0,
            noRecord: false,
            selectedView: this.listType[0],
            IsShowCustomCard: false,
            HeaderButtonArrayForUpdate: this.HeaderButtons,
            DisabledHeaderButton: [],
            widgetClassArray: ["one_wid", "two_wid", "three_wid", "four_wid", "five_wid"]           
        }
    },
    watch:{
        TotalRecords: function(v){
            var ths = this;
            setTimeout(() => {
                ths.TotalRecordsForPaging = v;
            }, (500));
        }
    },
    mounted: function(){
        var ths = this;
        const slots = useSlots()
        this.IsShowCustomCard = !!slots.customcard;
        this.DisabledHeaderButton = this.HeaderButtons.filter((x) => { return x.isdisabled == true });
    },
    updated: function(){
        if(this.ListData && this.ListData.length > 0){
            this.noRecord = false;
        }else{
            this.noRecord = true;
        }
        this.ErrorText = "";
        if(typeof(this.ListData) === 'undefined'){
            this.ErrorText = 'Data is not provided to the TgList component';
        } 
        else if(typeof(this.ListData) !== 'object' && typeof(this.ListData) !== 'array'){
            this.ErrorText = 'Data is not an object or array - TgList';
        }
        if(typeof(this.HeaderData) === 'undefined'){
            this.ErrorText = 'Headers are not provided to the TgList component';
        } 
        else if(typeof(this.HeaderData) !== 'object' && typeof(this.HeaderData) !== 'array'){
            this.ErrorText = 'Header Data is not an object or array - TgList';
        }
        if(typeof(this.IdentityColumn) === 'undefined'){
            this.ErrorText = 'Identity Column are not provided to the TgList component';
        }
        if(typeof(this.ModuleName) === 'undefined'){
            this.ErrorText = 'Module Name are not provided to the TgList component';
        }
        if(typeof(this.SubModuleCode) === 'undefined'){
            this.ErrorText = 'Sub Module Code are not provided to the TgList component';
        }
        if(typeof(this.RenderRowActionMethod) === 'undefined' &&  this.IsShowAction == 'true'){
            this.ErrorText = 'Render Row Action Method are not provided to the TgList component';
        }
        this.BindActionButtonEvent();
    },
    methods: {
        ExpandCollapseList() {
            var element1 = document.getElementById("crmleftsearch");
            element1.classList.toggle("left-menu-tab-collapsed");
            var element2 = document.getElementById("crmrightsearch");
            element2.classList.toggle("right-content-expand");
        },
        RefreshManageView(){
            this.$emit('RefreshManageView');
        },
        commonHeaderButtonClick: function (event) {
            this.$emit('HeaderButtonClick', event);
        },
        leftCommonSearch: function (value) {
            this.$emit('LeftsearchButtonClick', value);
        },
        pagerMethod: function (value) {
            this.$emit('PagerButtonClick', value);
        },
        sortdata: function (s) {
            this.$emit('SortdataButtonClick', s);
        },
        actionButtonClick:function(callbackfunction,item,index,event){
            this.$emit('ActionButtonClick', callbackfunction, item, index, event);
        },
        ChangeViewEvent: function (value) {
            try {
                this.selectedView = value;
                if(value == 'Kanban' && this.KanbanDataCallBackFunction != null){
                    this.KanbanDataCallBackFunction();
                }
                else if(this.TotalRecords > 0) {
                    // this is method is triggered to reset paging. (We have ro reset the paging as well when view is changed)
                    this.$emit('PagerButtonClick', {PageNumber: 1, PageSize: 10});
                }
                else {
                    this.ListDataCallBackFunction();            
                }
            } catch(x){
                console.log(x);
            }
        },
        onChangeEventOfCheckbox: function(e, row){
            if(e.target.checked){
                this.HeaderButtonArrayForUpdate.forEach((x) => {x.isdisabled = false})
            } else {
                let makeDeiabled = true;
                this.ListData.forEach((r) =>{
                    if(r.isSelected){
                        makeDeiabled = false;
                    }
                });
                if(makeDeiabled){
                    this.HeaderButtonArrayForUpdate.forEach((x) => {
                        this.DisabledHeaderButton.forEach((y) => {
                            if(y.title == x.title){
                                x.isdisabled = true;
                            }
                        })
                    });
                }
            }
        },
        BindActionButtonEvent: function() {
            let actionBUtton =Array.from(document.getElementsByClassName("action_icon"));
            actionBUtton.forEach(function (x) {
                x.removeEventListener("click", null);
                x.addEventListener("click", function (e) {
                    let actionBUttonClose =Array.from(document.getElementsByClassName("action_icon"));
                    actionBUttonClose.forEach(function (y) {
                        y.nextSibling.classList.remove("is-visible");
                        y.nextSibling.style.opacity = 0;
                        y.nextSibling.style.width = 0;
                    });
                    var checkText = x.nextSibling;
                    let id = null;
                    const elem = checkText
                    let pos = 0, width = 90;
                    clearInterval(id);
                    elem.classList.add("is-visible");
                    let actionClicks=Array.from(elem.getElementsByClassName("actions-onclick"))
                    actionClicks.forEach(function (e) {
                        width = width + e.clientWidth + 10;
                    });
                    id = setInterval(frame, 5);
                    function frame() {
                        if (pos > width) {
                            clearInterval(id);
                        } else {
                            pos = pos + 5;
                            elem.style.width = pos + "px";
                        }
                    }
                    elem.style.opacity = 1;
                });
                x.nextSibling.getElementsByClassName("close-action")[0].removeEventListener("click", null);
                x.nextSibling.getElementsByClassName("close-action")[0].addEventListener("click", function (e) {
                    x.nextSibling.classList.remove("is-visible");
                    x.nextSibling.style.opacity = 0;
                    x.nextSibling.style.width = 0;
                });
            });
        }
    
    }
}
</script>
<style >
    .action-list-box {
        opacity: 0;
        height: 0px;
        transition: opacity 1s;
    }
    .is-visible { 
        display: inline-block !important;
        height: auto;
        overflow: hidden !important;
        height: 46px;
    }
</style>