<script>
export default {
  props: {
     fieldInfo: {
                type: Object,
                required: true
            }            
  },
  data: function () {
    return {
      checkOptions: [],
    };
  },
  methods: {},
  created: function () {
    var self=this;
   var optionslist= this.fieldInfo.picklist_options
    if (optionslist != null && optionslist!=undefined && optionslist!='') {
      var splitted = optionslist.split(',');
    splitted.forEach(element => {
        self.checkOptions.push({
            name:element.split('|')[0],
          value: element.split('|')[1],
        });
    });
      console.log(self.checkOptions);
    }
  },
};
</script>
<template>
  <Field
    :name="GetFormSchemaFieldName(fieldInfo.label,fieldInfo.display_name)"
    :rules="{'required':fieldInfo.is_required?true:false}"
    :label="fieldInfo.name"
    v-slot="{errors,touched,invalid }" type="checkbox">
    <label v-for="item in checkOptions" :key="item">
      <input type="checkbox" :name="item.name" v-model="item.value" /> {{item.name}}
    </label>
    <span   class="invalid-feedback d-block">{{errors[0]}}</span>
  </Field>
</template>