<script>
import DataService from "../../services/DataService";
    export default {
        id: "list-header-buttons",
        props: {
            icons: {
                type: Array,
                required: true,
            },
            submodulecode: {
                type: String,
                required: true,
            },
            modulename: {
                type: String,
                required: true,
            }, 
            callbackfunction: {
      type: Function,
     
    },
        },
        data: function () {
            return {
                Fields: this.fields, 
                ViewList: [],
              ViewListId: '0',
            };
        }, 
        created: function () {
            var ths = this;
         ths.GetDdlRecords_ViewList();
        },
        mounted(){
            $(".invaction").click(function () {
        $(".action-list-box-ddl").show("slow");
    });
    $("#closeaction").click(function () {
        $(".action-list-box-ddl").hide("slow");
    });
        },
        methods: {
            ClickEvent: function (item) {
                this.$emit('ButtonClick', item);
            },
          
            GetDdlRecords_ViewList: function () {
              var vm = this;
            var url = `moduleName=${vm.modulename}&subModuleCode=${vm.submodulecode}&viewId=0&pageSize=10&pageNum=1&sortBy=&sortExp=`;
            DataService.GetViewList(url).then(function (response) {
                 vm.ViewList = response.data.DATA;
                    var chkVACTIVE = 0;
                    if (vm.ViewList == null || vm.ViewList == "") {

                    }
                    else {
                        vm.ViewList.forEach(function (item) {
                          
                            if (item.IS_SLECTED_USER_ID > 0) {
                                chkVACTIVE = chkVACTIVE + 1;
                                vm.ViewListId = item.VIEW_ID;
                            }
                        });
                    }
                        var indx = 0;
                        if (chkVACTIVE > 0) {
                            vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 0, "CAN_EDIT": 0, "ROWNUMBER":0, "VIEW_TYPE": "", "USER_LIST": "" });
                        }
                        else {
                            if (vm.ViewList == null || vm.ViewList == "") {
                                vm.ViewList = [];
                                vm.ViewList.push({ "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                            }
                            else {
                                vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });

                            }
                            vm.ViewListId = 0;
                        }
                   
                });
            },
            onchangeView: function (evt) {
                var vm = this;
                if (vm.ViewListId == '0') {
                    vm.DefaultView()
                }
                else {
                    var obj = {
                        ViewId: vm.ViewListId,
                        ModuleName: vm.modulename,
                        SubmoduleCode: vm.submodulecode
                    };
              
                var objectPostString = JSON.stringify(obj);
            
                DataService.ApplyView(objectPostString).then(function (response) {
                 
                    if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            vm.ShowAlert(vm.$t("ApplySuccess",[vm.$t("View")]),'success',true,vm.$t('Alert'));
                            vm.$emit('RefreshManageView');
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]),'failure',true,vm.$t('Alert'));
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]),'failure',true,vm.$t('Alert'));
                    }
                });

                }
            },
            DefaultView: function () {
                var vm = this;
                var obj = {
                    ModuleName: vm.modulename,
                    SubmoduleCode: vm.submodulecode
                };
                var objectPostString = JSON.stringify(obj);
                DataService.ClearView(objectPostString).then(function (response) {
                 if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            vm.ShowAlert(vm.$t("ClearSuccess",[vm.$t("DefaultView")]),'success',true,vm.$t('Alert'));
                            vm.$emit('RefreshManageView');
                        }
                        else {
                            vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"),'failure',true,vm.$t('Alert'));
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"),'failure',true,vm.$t('Alert'));
                    }
                });
            }
        }
    };
</script>
<template>
    
    <div class="theme-primary partition p-actions-expand" style="overflow:visible;">
                    <span id="titleName" class="p-name text-white px-2">
                        <select class="form-control customized_ddl" tabindex="1" v-model="ViewListId"  name="ViewList" v-on:change="onchangeView($event)">
                            <option v-for="(item,itemIndex) in ViewList" v-bind:value="item.VIEW_ID" :key="itemIndex">{{item.VIEW_NAME}}</option>
                        </select>
                    </span>
                    <span class="cursor-pointer">
                        <i class="fa fa-pencil-square-o mt-3 text-white font-20" :data-original-title="$t('ManageView')" href="javascript:;" v-on:click="ClickEvent({
        title: 'ManageView',
        iconClass: 'fa-list-alt',
        callbackfunction: 'ManageView',
        additionalClass: '',
        isdisabled: false,
        isvisible: true,
        checkPrivilege: false
    })" :title="$t('ManageView')"></i>
                    </span>

                    <span class="p-actions float-right">
                        <template v-for="(item,itemIndex) in icons" >
                            <RouterLink :key="itemIndex" :to="item.href" v-if="item.isvisible  &&  typeof item.callbackfunction == 'undefined' && item.title=='AddLead'" :id="item.id" :Key="item"
                            v-bind:class="{'p-action-btn text-white ': true, 'disabled': item.isdisabled }"  
                            v-bind:title='$t(item.title)'
                            v-bind:checkPrivilege="(item.checkPrivilege ? 'yes':'')">
                    <em v-bind:class="'fa ' + item.iconClass"><br /></em><br /><span> {{$t(item.title)}}</span>
                </RouterLink>
                <a v-bind:class="{'p-action-btn text-white ': true, 'disabled': item.isdisabled }" :id="item.id" :Key="item"
                   v-on:click="ClickEvent(item)" 
                   v-if="item.isvisible  &&  typeof item.callbackfunction != 'undefined' && (item.title=='Delete'||item.title=='Filters' || item.title=='Reset')"
                   v-bind:title='$t(item.title)' 
                   href="javascript:;">
                   <em v-bind:class="'fa ' + item.iconClass"></em><br /><span>{{$t(item.title) }}</span>
                </a>
              
            </template>
                        <a class="p-action-btn text-white invaction" href="javascript:;" :title="$t('Action')"><i class="fa fa-plus" aria-hidden="true"></i><br>{{$t('Action')}}</a>
                    </span>

                    <span class="action-list-box-ddl expand" style=" z-index: 999; padding: 0px 10px; right: 0px; line-height: 16px; display: none; position: absolute; top: 50px; color: rgb(17, 17, 17); overflow: auto; width: 224px;"> 
                        <span class="list-actions w-100 fsm-list" id="action-list" style="font-size: 14px;">
                            <template v-for="(item,itemindex) in icons" >
                <a :key="itemindex" v-bind:class="{'actions-onclick ': true, 'disabled': item.isdisabled }" :id="item.id" :Key="item"
                   v-on:click="ClickEvent(item)" 
                   v-if="item.isvisible  &&  typeof item.callbackfunction != 'undefined' && (item.title=='Page Tour'||item.title=='ExportToExcel' ||item.title=='ChangeLeadOwner'||item.title=='AdditionalOwner'||item.title=='SendEmail'||item.title=='CloseLeads') "
                   v-bind:title='$t(item.title)' 
                   href="javascript:;"><br/>
                   <em v-bind:class="'fa ' + item.iconClass"></em><br /><span>{{$t(item.title) }}</span>
                </a>
                <RouterLink :to="item.href" v-if="item.isvisible  &&  typeof item.callbackfunction == 'undefined' && item.title=='ImportLeads'" :id="item.id" :Key="item"
                            v-bind:class="{'actions-onclick ': true, 'disabled': item.isdisabled }"  
                            v-bind:title='$t(item.title)'
                            v-bind:checkPrivilege="(item.checkPrivilege ? 'yes':'')"><br/>
                    <em v-bind:class="'fa ' + item.iconClass"><br /></em><br /><span> {{$t(item.title)}}</span>
                </RouterLink>
            </template>
            <i class="fa fa-times close close-action" id="closeaction" aria-hidden="true"></i>
                        </span>
                     </span>
                <span class="collapse-head-btns">
                        <a href="javascript:;" class="collapse-btns"><i class="fa fa-angle-up text-white"></i></a>
                    </span>
                </div>
</template>
<style scoped>
.fsm-list a {
    width: 100%;
    text-align: left;
    padding: 8px 10px;
    border-bottom: 1px solid #ccc;
}
.actions-onclick {
    color: #303030;
}
.actions-onclick {
    float: left;
    padding: 3px 10px;
    line-height: 16px;
    font-weight: 400;
    transition: background-color 0.5s ease;
}
.action-list-box-ddl a.actions-onclick i {
    width: 30px;
    text-align: center;
}

.action-list-box-ddl a.actions-onclick.disabled i{opacity:0.5;}
.action-list-box .actions-onclick{margin-top:5px;}



</style>

